@import url('//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css2?family=Concert+One&display=swap');

.shopping-list {
    margin-left: 80px;
    margin-right: 80px;
    padding-top: 70px;
    padding-bottom: 80px;
    position: relative;
}

.title {
    font-size: 40px;
    font-family: 'Concert One', cursive;
    padding-bottom: 20px;
    display: inline-block;
}

.fa-plus {
    border: 2px solid black;
    margin: 10px 10px 10px 20px;
    padding: 4px 2px 2px 2px;
    width: 40px;
    height: 40px;
    text-align: center;
    border-radius: 25%;
    font-size: 30px;
}

.fa-plus:hover {
    color: black;
    background-color: #22506098;
    border: 2px solid #225060;
}

.row_cont {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.row {
    width: 100%;
    margin: 0px 0px 0px 0px;
    align-items: center;
}

.col-lg-3, .col-md-5, .col-sm-7, .col-11 {
    padding: 0;
    width: 300px;
}

.cell {
    margin: 20px 20px 20px 20px;
    width: 100%;
    display: inline-block;
    align-items: center;
    border-radius: 5px;
    border: 1px solid #225060;
}

img {
    width: 100%;
}

.item {
    width: 100%;
    height: 300px;
    margin: 0px auto;
    float: left;
}

.details {
    border: 1px solid #225060;
    height: 100%;
}
