nav a {
    text-decoration: none;
}

.navbar-brand {
    font-size: 25px;
}

.navbar {
    border-radius: 0%;
    background-color: #225060;
    font-family: 'Concert One', cursive;
    font-size: 18px;
}

.navbar-dark .navbar-toggler {
    border-color: white;
}

nav a {
    color: white;
    margin: 0px 10px 0px 10px;
    padding: 0px 5px 0px 5px;
}

nav a:hover {
    text-decoration: none;
    color: white;
}

.navbar-nav a:hover {
    border-bottom: 3px solid white;
}

.btn-outline-dark {
    border-color:  #225060;
}

.btn-outline-dark:hover {
    background-color:  #225060;
    color: white;
}

.inventory {
    margin-left: 80px;
    margin-right: 80px;
    padding-top: 70px;
    padding-bottom: 80px;
    position: relative;
}

.title {
    font-size: 40px;
    font-family: 'Concert One', cursive;
    padding-bottom: 20px;
    display: inline-block;
}

.fa-plus {
    border: 2px solid black;
    color: black;
    margin: 10px 10px 10px 20px;
    padding: 4px 2px 2px 2px;
    width: 40px;
    height: 40px;
    text-align: center;
    border-radius: 25%;
    font-size: 30px;
}

.fa-plus:hover {
    color: black;
    background-color: #22506098;
    border: 2px solid #225060;
}

.form {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.form-control {
    width: 100%;
    height: 40px;
    border-color: #225060;
    text-indent: 40px;
}

.search {
    width: 100%;
    position: relative;
    color: #225060;
    font-size: 18px;
    margin-left: auto;
    margin-right: auto;
}

.fa-search { 
    position: absolute;
    top: 11px;
    left: 25px;
}

.row_cont {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.row {
    width: 100%;
    margin: 0px 10px 0px 10px;
}

.cell {
    margin: 20px 20px 20px 20px;
    align-items: center;
    border-radius: 5px;
    border: 1px solid #225060;
}

.card-img-top {
    border-bottom: 1px solid #225060;
}

.card-body {
    text-align: center;
    padding: 10px;
}
.card-text {
    font-size: 14px;
}


.content {
    margin-left: 100px;
